@import 'global';

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}
html,
body {
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: sans-serif;
}

div#main-container {
  display: flex;
  width: 70%;
  height: 92%;

  position: absolute;
  z-index: 10;
  top: 7.5%;
  left: 22.5%;
}

#review-container {
  width: 100%;
  max-width: 150rem;
  height: 57rem;
  margin-top: 2rem;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

#review-title {
  background-color: #000000b0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 85%;
  max-width: 80rem;
  height: 2.7rem;
  font-size: 1.4rem;
  font-weight: 600;
  color: aliceblue;
}
h3 {
  margin: 0;
}
#review-content {
  background-color: #c4c4c4;
  width: 85%;
  max-width: 80rem;
  height: 45rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#feedback-radio {
  display: flex;
  padding: 2%;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  max-width: 80rem;
  height: 4.5rem;
  background-color: #00000075;
}

.radio {
  width: 17rem;
  height: 5rem;
  padding: 2%;
  min-width: 12rem;
  display: flex;
  color: aliceblue;
  justify-content: space-evenly;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 100;
}

div#container-label {
  display: flex;
  text-align: center;
  height: 4.5rem;
  width: 12rem;
  text-align: center;
  padding: 1%;
}

.checkbox-avaliacao {
  width: 1.5rem;
  height: 5rem;
}

#feedback-text {
  width: 65rem;
  margin-top: 5rem;
  height: 30rem;
  font-size: 1.6rem;
  font-family: sans-serif;
  font-weight: 300;
  width: 80%;
}

textarea {
  width: 100%;
  height: 100%;
  padding: 3rem;
}

button#review-button {
  background: #4f4f4f;
  width: 13rem;
  height: 3rem;
  color: aliceblue;
  font-size: 1.3rem;
  font-family: sans-serif;
  font-weight: 500;
  border-radius: 0.4rem;
}

button#review-button:hover {
  cursor: pointer;
}

div#button-review {
  width: 85%;
  max-width: 80rem;

  display: flex;
  justify-content: flex-end;
}

@media (max-width: 1000px) {
    
  #global-container {
    margin: 0;
  }

  div#main-container {
    width: 100%;
    height: 56.6rem;
    left: 0;

    position: initial;
  }

  #review-container {
    width: 100%;
    max-width: 150rem;
    height: 54rem;
    margin-top: 2rem;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  #review-title {
    background-color: #000000b0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75%;
    max-width: 80rem;
    height: 2.7rem;
    font-size: 1.4rem;
    font-weight: 600;
    color: aliceblue;
  }
  h3 {
    margin: 0;
  }
  #review-content {
    background-color: #c4c4c4;
    width: 75%;
    max-width: 80rem;
    height: 45rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  #feedback-radio {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 80rem;
    height: 4rem;
    background-color: #00000075;
  }

  .radio {
    width: 17rem;
    min-width: 12.8rem;
    display: flex;
    color: aliceblue;
    justify-content: space-evenly;
    align-items: center;
  }

  #teste {
    display: flex;
    justify-content: space-between;
  }

  div#container-label {
    width: 80%;
    min-width: 1rem;
    text-align: center;
  }

  .checkbox-avaliacao {
    width: 1.5rem;
    height: 5rem;
  }

  #feedback-text {
    width: 65rem;
    margin-top: 5rem;
    height: 30rem;
    font-size: 1.6rem;
    font-family: sans-serif;
    font-weight: 300;
    background-color: #00000075;
    width: 80%;
  }

  textarea {
    width: 100%;
    height: 100%;
    padding: 3rem;
  }

  button#review-button {
    background: #4f4f4f;
    width: 13rem;
    height: 3rem;
    color: aliceblue;
    font-weight: 600;
    border-radius: 0.4rem;
  }

  button#review-button:hover {
    cursor: pointer;
  }

  div#button-review {
    width: 75%;
    max-width: 80rem;

    display: flex;
    justify-content: flex-end;
  }
}
