@import '_global';

.statistics-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 15%;
  left: 28%;
  font-size: 1.8rem; 
  
  .statistics-title{
    background-color: $grey-button;
    display: flex;
    align-items: center;
    justify-content: end;
    color: white;
    font-size: 1rem;
    height: 3rem;
    width: 100% ;
    text-align: center;

    select {
      margin-left: 20rem;
      margin-right: 1rem;
    }
  }
}

.statistics{
  display: flex;
  flex-direction: column;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 2.0rem;
  margin: 8.0rem 0;
}

.cards {
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 7px 7px 13px 0px rgba(50, 50, 50, 0.22);
  padding: 2.5rem;
  height: 23.0rem;
  width: 25.0rem;
  align-items: center;
  margin: 1.8rem;
}

.cards2 {
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 7px 7px 13px 0px rgba(50, 50, 50, 0.22);
  padding: 2.5rem;
  height: 23.0rem;
  width: 25.0rem;
  align-items: center;
  margin: 1.8rem;
}

.line1 {
  display: flex;
}

.line2 {
  display: flex;
}

.cards:nth-child(1) {
  background-color: rgba(26, 148, 247, 0.527);
}
  
.cards:nth-child(2) {
  background-color: rgba(184, 26, 247, 0.425);
}

.cards:nth-child(3) {
  background-color: rgba(29, 252, 0, 0.541);
}

.cards2:nth-child(1) {
  background-color: rgba(247, 243, 3, 0.705);
}
  
.cards2:nth-child(2) {
  background-color: rgba(250, 103, 4, 0.767);
}

.cards2:nth-child(3) {
  background-color: rgba(253, 1, 98, 0.596);
}






